import React from "react";
import { MenuItem, Button, IconButton, Badge } from "@mui/material";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import PropTypes from "prop-types";
import { Link as RouterLink } from "react-router-dom";

export default function LinkButtonPublic(props) {
	const {
		children,
		primary,
		secondary,
		to,
		alternative,
		target,
		type,
		icons,
		iconSRC,
		badgeContent,
		...rest
	} = props;

	let url = `${to}`.split(":");

	const renderLink = React.useMemo(
		() =>
			React.forwardRef((itemProps, ref) => (
				<RouterLink
					to={url[url.length - 1]}
					target={target}
					ref={ref}
					{...itemProps}
				/>
			)),
		[url, target]
	);
	switch (props.type) {
		case "menu":
			return (
				<MenuItem {...rest} component={renderLink} title={primary}>
					{children}
				</MenuItem>
			);
		case "list":
			return (
				<ListItem
					button
					key={primary}
					component={renderLink}
					title={primary}
					{...rest}
				>
					{icons ? <ListItemIcon {...rest}>{iconSRC}</ListItemIcon> : ""}
					<ListItemText primary={primary} secondary={secondary} {...rest} />
				</ListItem>
			);
		case "badge":
			return (
				<IconButton {...rest} component={renderLink} title={primary}>
					<Badge
						badgeContent={badgeContent}
						overlap="circular"
						anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
					>
						{children}
					</Badge>
				</IconButton>
			);

		default:
			if (icons) {
				return (
					<IconButton {...rest} component={renderLink} title={primary}>
						{children}
					</IconButton>
				);
			} else {
				return (
					<Button {...rest} component={renderLink} title={primary}>
						{children}
					</Button>
				);
			}
	}
}

LinkButtonPublic.defaultProps = {
	icons: false,
	iconSRC: "",
	target: "_self",
	type: "button",
	primary: "",
	secondary: "",
	badgeContent: "",
};
LinkButtonPublic.propTypes = {
	to: PropTypes.string.isRequired,
	target: PropTypes.string,
};
